body {
  background-color:lightcyan;
  text-align:center;
  align-items: center;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
#root{
  flex-grow: 1;
  display: flex;
  justify-content: center;
  align-items: center;
}
.app{
  width: 100%;
}
.header p{
  margin-top: 0px;
  top: 0px;
  width: 100%;
  background-color: black;
  color: aqua;
  font-family: Courier, monospace;
}
.fooder{
  position:fixed;
  left:0px;
  bottom:25px;
  height:30px;
  width:100%;
  text-align: center;
  font-family: Courier, monospace;
}
.fooder p{
  background-color: black;
  color:aqua; 
  text-align: center;
  align-items: center;
}
h2{
  background-color: black;
  color: aliceblue;
  height: 40px;
  
}

p{
  padding: 10px 10px 10px 10px;
  background-color: aquamarine;
}
button{
  color: blueviolet;
  background-color: bisque;
  border-radius: 20px;
}
main{
  text-align: start;
  justify-self: center;
  align-items: center;
}
.contantbody{
  margin-bottom: 45px;
  /* border: 1px solid; */
  width: 90%;
  padding: 0px;
  justify-content: center;
  align-items: center;
  position: relative;
}
.contantbox{
 padding: 0px;
}
.item{
  width: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  font-size: 30px ;
  background-color: cadetblue;
  margin-bottom: 5px;
  list-style-type: none;
  border-radius: 5px;
  box-shadow: 2px 2px 2px;
}
.item input[type="checkbox"]{
  background-color: blue;
  text-align: center;
  width: 2rem;
  width: 30px;
  max-width: 30px;
  height: 2rem;
  height: 30px;
  max-height: 30px;
  cursor: pointer;
  margin-left:5px ;
}

.item > label{
  color: black;
  font-size: 1em;
  padding: 5px;
  width: 80%;
  overflow: visible;
  overflow-wrap: anywhere;
  text-align: start;
}

.item svg{
  width: 40px;
  max-width: 40px;
  color:darkslategrey;
  position: absolute;
  right: 0px;
}
.item svg:focus,
.item svg:hover{
  color: brown;
  outline: none;
}
.searchinput{
  height: 30px;
  width: 300px;
  display: flex;
  margin-left: 40px;
  border-radius: 2px;
  outline: 0mm;
}
.additem{
  text-align: start;
  padding: 5px;
}

.addlable{
position: absolute;
left: -99999px;

}
#additeminput{
  height: 30px;
  width: 200px;
  margin: 0px 10px 0px 35px ;
}
.additem button{
  height: 40px;
  width: 40px;
  cursor: pointer;
}
.additem button:hover{
  color: aqua;
  background-color: lawngreen;
}

